@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
.App {
    text-align: center;
    background-image: url(/static/media/background.5fe9c32a.jpg) !important;
    background-size: 100% 100%;
  }

  
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      -webkit-animation: App-logo-spin infinite 20s linear;
              animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
.navbar {
    background-color: #ffffff4b !important;
}
.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}
.navbar img {
  border-radius: 50%;
  width: 25px;
  height: 25px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  border-color: white;
  background-color: rgba(255, 255, 255, 0.507);
}
* {
  box-sizing: border-box;
}
body {
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}



.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  
  
  background: #ffffff6b;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  -webkit-transition: all .3s;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: lightgreen;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: rgb(24, 109, 24);
}
.btn-primary {
  background-color: rgb(24, 109, 24);
}

.auth-wrapper .Welcomebox {
  justify-content: center;
  text-align: center;
}

.auth-wrapper .btn-primary {
  width: 40%;
  padding: 5px;
  margin-left: 5px;
}

.newuser{
  background-color: green;
}

.auth-inner img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  border-color: white;
  background-color: rgba(255, 255, 255, 0.507);
}

#containerLeft{
  background-color: yellow;
}

#containerRight {
  background-color: yellow;
}




